.root {
    transform-origin: top left;
    position: relative;
    width: 390.0002px;
    height: 843.9999px;
    align-items: flex-start;
    background-color: #000;
    overflow: hidden;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  .group_background {
    transform-origin: top left;
    position: absolute;
    left: calc(50% - -0.4999px);
    top: 0px;
    width: 389px;
    height: 440px;
    align-items: flex-start;
    background-image:/* webpackIgnore: true */ url('/assets/group_background.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size:cover;
    overflow: hidden;
    transform: matrix(1, 0, 0, 1, 0, 0) translateX(-50%);
  }
  .group_Foreground {
    position: absolute;
    left: 0;
    top: 0;
    width: 389px;
    height: 440px;
    background-image: linear-gradient(180deg, #0000 0%, #000f 100%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
  }
  .group_arrow1 {
    position: absolute;
    left: 19px;
    top: 34px;
    width: 25px;
    height: 24px;
